<template>
  <div id="app">
    <header>
      <div>
        <h2>{{ first_name }} {{ last_name }}</h2>
      </div>
    </header>
    <div class="wp-block-column is-layout-flow wp-block-column-is-layout-flow" style="flex-basis:1%">
      <div class="sidebar-parent-block">
        <nav class="sidebar" id="sidebar">
          <!-- содержимое навигации -->
        </nav>
      </div>
    </div>
    <main>
      <h1>Расписание на <span id="current-date">{{ currentDate }}</span></h1>
      <input type="text" class="group-input" id="group" v-model="group" placeholder="Введите группу" value="ИС-208" />
      <input type="date" class="date-input" id="date" v-model="date" @change="fetchSchedule" />
      <div class="nav-buttons">
        <button @click="changeDate(-1)">&#60;=</button>
        <button @click="changeDate(1)">=&#62;</button>
      </div>
      <div class="schedule" id="schedule">
        <div v-for="group in scheduleData" :key="group.group_name" class="group">
          <h2>{{ group.group_name }}</h2>
          <div v-for="lesson in group.lessons" :key="lesson.index" class="lesson">
            <div class="index-div"><p class="index">{{ lesson.index || 'Н' }}</p></div>
            <div>
              <strong>{{ lesson.subject_name || 'Без названия' }}</strong><br>
              Аудитория: {{ lesson.cabinet || 'Нет информации' }}, Корпус: {{ lesson.building || 'Нет информации' }}<br>
              Преподаватель: {{ lesson.teachers.map(teacher => teacher.name).join(', ') || 'Нет информации' }}<br>
              <button class="details-button" @click="toggleDetails(lesson)">Показать детали</button>
              <div v-if="lesson.showDetails" class="details">
                <p>Домашнее задание: {{ lesson.homework || 'Нет информации' }}</p>
                <p>Оценка: {{ lesson.grade || 'Нет информации' }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      first_name: 'Имя',
      last_name: 'Фамилия',
      date: new Date().toISOString().split('T')[0],
      currentDate: new Date().toISOString().split('T')[0].split('-').reverse().join('.'),
      group: 'ИС-208',
      scheduleData: []
    };
  },
  methods: {
    toggleEditMode() {
      alert('Режим редактирования включен/выключен');
    },
    toggleSidebar() {
      const sidebar = document.getElementById('sidebar');
      sidebar.style.display = sidebar.style.display === 'block' ? 'none' : 'block';
    },
    fetchSchedule() {
      fetch(`https://апи.пары.ркэ.рф/api/schedules/public?date=${this.date}&group=${this.group}`)
        .then(response => response.json())
        .then(data => {
          this.scheduleData = data.schedules;
        })
        .catch(error => {
          console.error('Ошибка:', error);
        });
    },
    toggleDetails(lesson) {
      lesson.showDetails = !lesson.showDetails;
    },
    changeDate(direction) {
      const currentDate = new Date(this.date);
      currentDate.setDate(currentDate.getDate() + direction);
      this.date = currentDate.toISOString().split('T')[0];
      this.currentDate = this.date.split('-').reverse().join('.');
      this.fetchSchedule();
    }
  },
  mounted() {
    this.fetchSchedule();
  }
}
</script>

